<template>
	<view class="wrapper padding">
		<image src="../../static/logo.png" class="logo"></image>
		<view class="txt">卫蓝购</view>
		<view class="btn">
			<button 
				class='cu-btn block lg'
				open-type="getPhoneNumber"
				@getphonenumber="getPhoneNumber"
				:disabled="!select"
			>
				<image src="../../static/wechat.png" class="img"></image>
				<text>微信一键登录</text>
			</button>
		</view>
		<view class="rule">
			<view class="checkbox" @click="select=!select">
				<image src="../../static/select.png" v-if="select"></image>
				<image src="../../static/unselect.png" v-else></image>
			</view>
			<view>我已阅读并同意</view>
			<view class="a" @click="goWeb">
				<text>《用户隐私协议》</text>
			</view>
		</view>
	</view>
</template>
<script>
	import {http2Https} from "../../common/tools.js"
	export default {
		data() {
			return {
				select:true,
				loginParams:{
					encryptedData:'',//	微信绑定的手机号加密字符串	String	是	
					sessionKey:'',//	当前会话id	String	是	
					iv:'',//用于解析加密的	String	是	
					openId:'',	//当前微信号的唯一标识	String	是	
					type:"",//roleType:3,// 1-管理员   2-员工   3-用户,
				},
				paramsInfo:{},
				url:''
			};
		},
		methods:{
			getPhoneNumber(e){
				if(e.detail.errMsg=="getPhoneNumber:ok"){
					this.loginParams.iv=e.detail.iv;
					this.loginParams.encryptedData=e.detail.encryptedData
					this.request("/user/login",this.loginParams).then((res)=>{
						
						if(res.resultCode==1009 || res.resultCode==1006){
							var nextPage=""
							var _data=res.data
							
							if(_data.ticket){ //已注册
								let userInfo={
									value:_data.ticket,
									time:new Date().getTime(),
									userType:this.loginParams.type
								}
								uni.setStorageSync('token', JSON.stringify(userInfo));
								uni.switchTab({
									url:`/pages/personal/info`
								})
								
								
							}else{ //未注册
								if(this.loginParams.type==1 || this.loginParams.type==2 ){
									nextPage="/pages/staff/login?"
								}else{
									nextPage="/pages/personal/register?fromType=1"
								}
								this.perfectNavigateTo({
									url:nextPage+`&authId=${_data.authId}&mobile=${_data.phone}&userType=${this.loginParams.type}`
								})
							}
						}else{
							uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常，请稍后重试',icon:'none'})
						}
					})
				}else{
					// uni.showToast({title:'授权失败',icon:'none'})
				}
			},
			checkSession(){
				uni.checkSession({
				  success () {
				    //session_key 未过期，并且在本生命周期一直有效
						return
				  },
				  fail:()=>{
				    this.getCode()
				  }
				})
			},
			getCode(){
				uni.login({
				  success:(res)=>{
				    if (res.code) {
				      //发起网络请求
				      this.request("/auth/getOpenIdAndSessionKey",{authCode:res.code,type:this.loginParams.type}).then((res)=>{
								if(res.resultCode==0){
									this.loginParams.openId=res.data.openId
									this.loginParams.sessionKey=res.data.sessionKey
								}else{
									uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
								}
							})
				    } else {
				      console.log('微信登录失败！' + res.errMsg)
				    }
				  }
				})
			},
			goWeb(){
				var _obj={
					url:this.url,
				}
				uni.setStorageSync('webViewInfo', _obj);
				this.perfectNavigateTo({
					url:"/pages/webView/webView"
				})
			},
			getCommon(){
				this.request("/common/param",{key:"member_reg_protocol"}).then((res)=>{
					if(res.data){
						this.url=http2Https(res.data)
					}
				})
			}
		},
		onShow(){
			this.checkSession()
		},
		onLoad(params){
			this.loginParams.type=params.roleType
			this.loginParams.openId=getApp().globalData.openId
			this.loginParams.sessionKey=getApp().globalData.sessionKey
			this.getCode()
			this.getCommon()
		}
	}
</script>

<style lang="stylus">
	.wrapper
		display:flex
		flex-direction column
		align-items center
		padding:0upx 60upx
		.logo
			width:214upx
			height:212upx
			margin-top:254upx
		.txt
			font-size:36upx;
			font-family:PingFangSC-Regular,PingFang SC;
			font-weight:400;
			color:rgba(30,30,30,1);
			margin-top:15upx
		.btn
			margin-top:138upx
			width:100%
			.lg
				height:96upx
				line-height 96upx
				background:#70D097
				color:#fff
				display:flex
				flex-direction row
				align-items center
				.img
					width:40upx
					height:32upx
					margin-right:10upx
		.rule
			display:flex
			flex-direction row
			align-items center
			margin-top:50upx
			font-size:22upx;
			font-family:PingFangSC-Regular,PingFang SC;
			font-weight:400;
			color:rgba(178,182,192,1);
			width: 98%;
			.checkbox
				margin-right:5upx
				display:flex
				padding:5upx
				flex-direction row
				align-items center
				image
					width:24upx
					height:24upx
			.a
				color:#8491A8
</style>
